html,
body {
  width: 100%;
  height: 100%;
  -webkit-user-select: none;
  /* 禁止选中文本（如无文本选中需求，此为必选项） */
  user-select: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
body {
  margin: 0; 
  padding: 0;
  font: 12px/1.5 'Heiti SC', '\9ED1\4F53', 'SimHei', '黑体';
}
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
form,
fieldset,
input,
p,
a,
blockquote,
th,
i,
u,
span {
  margin: 0;
  padding: 0;
  font-weight: normal;
}
ol,
ul {
  list-style: none;
}
u,
i {
  font-weight: normal;
  font-style: normal;
}
li {
  list-style-type: none;
}
img {
  vertical-align: top;
  border: 0;
}
a {
  color: #333;
  text-decoration: none;
}
a,
img {
  -webkit-touch-callout: none;
  /* 禁止长按链接与图片弹出菜单 */
}
input,
select,
button,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  border: none;
  background-color: #fff;
  margin: 0;
  padding: 0;
  resize: none;
  outline: none;
  border-radius: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0px;
}
.clearfix {
  *zoom: 1;
}
.clearfix:after {
  display: block;
  overflow: hidden;
  clear: both;
  height: 0;
  visibility: hidden;
  content: ".";
}
.wrap{ width: 1000px; margin: 0 auto;}